<template>
    <div class="countries">
        <vue-headful
            :title="'Coronavirus cases in ' + countrydata.country + ' : ' + countrydata.cases "
            :description="'Coronavirus has effected ' + countrydata.cases + ' people in ' + countrydata.country "
            og:url="https://worldstatsinfo.com/"
            og:type="website"
            :og:title="'Coronavirus has effected ' + countrydata.cases + ' people in ' + countrydata.country + ' | WorldStatsInfo' "
            :og-description="'Coronavirus has effected ' + countrydata.cases + ' people in ' + countrydata.country "
            og:site_name="World Stats Info"
            og:image="https://worldstatsinfo.com/og-image.png"
            robots="index,follow"
        />
        <div class="m-3">
            <!-- <div>{{countrydata.data.country}}</div> -->
            <div class="row d-flex justify-content-center">
                <a href="/" class="m-3 text-primary"><i class="material-icons md-18">arrow_back</i> back to home</a>
            </div>
            <div class="row d-flex justify-content-center">
                <img :src="countrydata.countryInfo.flag" style="width:20rem;" alt="" srcset="">
            </div>
            <h2 class="text-center">{{ countrydata.country}}</h2>
            <div class="">
                <h3 class="m-0 text-center">{{countrydata.cases}}</h3>
                <p  class="m-0 text-center text-secondary">Total Cases</p>
            </div>
            <!--  -->
            <div class="row d-flex justify-content-center">
                <div class="col-md-4">
                  <line-chart class="bg-cases-chart rounded p-2" xmin="2020-02-31" :colors="['#123f3e', '#666']" :data="indiahistory.timeline.cases"></line-chart>
                  <p class="text-center">Cases in {{countrydata.country}}</p>
                </div>
                <div class="col-md-4">
                  <line-chart class="bg-deaths-chart rounded p-2" xmin="2020-02-31" :colors="['#b00', '#666']" :data="indiahistory.timeline.deaths"></line-chart>
                  <p class="text-center">Deaths in {{countrydata.country}}</p>
                </div>
            </div>
            <!--  -->
            <div class="row m-3">
                <div class="col d-flex justify-content-sm-center mt-4 justify-content-md-end">
                    <div class="card bg-danger shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">+ {{countrydata.todayCases}}</p>
                        <p class="h5 text-center text-white">Today Cases</p>
                    </div>
                </div>
                <div class="col d-flex justify-content-sm-center mt-4 justify-content-md-start">
                    <div class="card bg-danger shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">+ {{countrydata.todayDeaths}}</p>
                        <p class="h5 text-center text-white">Today Deaths</p>
                    </div>
                </div>
            </div>
            <div class="row m-3">
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-end">
                    <div class="card shadow bg-danger div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.deaths}}</p>
                        <p class="h5 text-center text-white">Total Deaths</p>
                    </div>
                </div>
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-start">
                    <div class="card shadow bg-success div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.recovered}}</p>
                        <p class="h5 text-center text-white">Total Recovered</p>
                    </div>
                </div>
            </div>
            <div class="row m-3">
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-end">
                    <div class="card bg-primary shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.active}}</p>
                        <p class="h5 text-center text-white">Active Cases</p>
                    </div>
                </div>
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-start">
                    <div class="card bg-warning shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.critical}}</p>
                        <p class="h5 text-center text-white">Critical Cases</p>
                    </div>
                </div>
            </div>
            <div class="row m-3">
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-end">
                    <div class="card bg-secondary shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.casesPerOneMillion}}</p>
                        <p class="h5 text-center text-white">Cases per One million</p>
                    </div>
                </div>
                <div class="col d-flex justify-content-sm-center mt-3 justify-content-md-start">
                    <div class="card bg-secondary shadow div-cards div-cards-recovered align-self-sm-center" style="width:20rem">
                        <p class="h4 text-center font-weight-light">{{countrydata.deathsPerOneMillion}}</p>
                        <p class="h5 text-center text-white">Deaths per One million</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="row d-flex justify-content-center m-2">
          <div class="col-md-7 div-more rounded">
            <h5>Useful Info:</h5>
            <ul>
              <li><a class="text-primary" href="/precautions">Precautions</a></li>
              <li><a class="text-primary" href="/symptoms">Symptoms</a></li>
              <li><a class="text-primary" href="/dos">Stop the spread</a></li>
            </ul>
          </div>
        </div>
        <br>
        <br>
        <br>
    </div>
</template>

<style>
.bg-cases-chart
{
    background-color: rgb(189, 255, 198);
}
.bg-deaths-chart
{
    background-color: rgb(252, 186, 186);
}
</style>

<script>
import axios from 'axios'
export default {
    data () {
    return {
      countrydata: [],
      indiahistory: []
    }
  },
    mounted()
    {
      axios
        .get('https://api.waytoskill.com/countries/' + this.$route.params.country)
        .then(response => (this.countrydata = response.data)).catch((error)=>{
            if (error.response.status === 404) {
            this.$router.push('/');
            }
        })
      axios
        .get('https://corona.lmao.ninja/v2/historical/' +this.$route.params.country)
        .then(response => (this.indiahistory = response.data, console.log(response.data)))
    }
}
</script>